import axios from 'axios'

let baseURL = 'http:/localhost:8000/api/'

export default class API {
    constructor(token) {
        this.request = axios.create({
            baseURL,
            headers: { 'Access-Token': token }
        })
    }

    auth(email, password) {
        return request.post('auth/token', {
            data: { email, password }
        })
    }
}



